.container{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 23.438rem;
  margin: auto;
}
.header{
  width: 100%;
  height: 4.188rem;
  border-bottom: 1px solid #C6C6C6;
  display: flex;
  align-items: center;
}
.logo{
  width: 7.938rem;
  height: 2.063;
  margin-left: 1em;
}
.avatar{
  width: 2.125rem;
  height: 2.125rem;
  margin-left: auto;
  margin-right: 1em;
}
.card:not(:last-child){
  border-bottom: 1px solid #C6C6C6;
}
.post-avatar{
  width: 2.125rem;
  height: 2.125rem;
  margin-left: 0.5em;
}
.info{
  display: flex;
  flex-direction: column;
  margin: 0.5em;
}
.name{
  font-size: 0.813rem;
  font-weight: 700;
  margin-bottom: 0;
  margin-top: 0.5em;
}
.location{
  font-size: 0.75rem;
  margin-top: 0;
  margin-bottom: 0.5em;
}
.card-top{
  display: flex;
  align-content: center;
  align-items: center;
  flex-direction: row;
  margin-left: auto;
}
.icons{
  margin: 1em 0.2em;
}
.likes{
  font-size: 0.813rem;
  margin-left: 0.2em;
  margin-top: 0;
  font-weight: 700;
  margin-bottom: 0;
}

.comment{
  font-size: 0.813rem;
}

.comment-name{
  font-size: 0.813rem;
  margin-left: 0.2em;
  font-weight: 700;
}